let typeArr = ['d', 'r', 'c']
let statusArr = ['warning', 'success', '']
let titleArr = ['', '', '']
let tipsArr = [
    `NPCWiki()`,
    `,npc, `,
    ``
]
let smTitleArr = ['', '', '']
let installBtn_config = typeArr.map((type, idx)=>{
    return {
        type:'primary',
        zhType: type,
        desc: titleArr[idx],
        title: titleArr[idx],
        size: 'small',
        status: statusArr[idx],
        tips:tipsArr[idx],
        smTitle: smTitleArr[idx],
        style:{
            margin: '5px'
        }
    }
})

export {
    installBtn_config
}